___CSS_LOADER_EXPORT___.push([module.id, `/* Main editor container */
.editor-container {
  width: 80%;
  margin: auto;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  position: relative;
  margin-top: 20px;
  text-align: left;
}

/* Toolbar styles */
.toolbar {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 10px;
  flex-wrap: wrap;
}

.toolbar .toolbarBtn,
.toolbar select {
  margin: 5px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}

/* Main editor area */
#editor {
  padding: 10px;
  min-height: 400px;
  overflow: auto;
  outline: none;
}

/* Individual editor blocks */
.editor-block {
  margin-bottom: 10px;
  padding: 10px;
  min-height: 40px;
}

/* Active editor block */
.editor-block.active {
  border: 1px solid blue;
}

/* Image styles within the editor */
img {
  max-width: 100%;
  height: auto;
}

/* Editor footer (word/character count) */
.editor-footer {
  position: relative;
  bottom: 0;
  left: 0;
  padding: 10px;
  background: rgba(255, 255, 255, 0.8);
  font-size: 12px;
}

.table-select {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: white;
  font-size: 14px;
  cursor: pointer;
}

.table-select:hover {
  border-color: #888;
}

.table-select:focus {
  outline: none;
  border-color: #0066cc;
  box-shadow: 0 0 0 2px rgba(0,102,204,0.2);
}`, "",{"version":3,"sources":["webpack://./component/src/styles/rich-text-editor.css"],"names":[],"mappings":"AAAA,0BAA0B;AAC1B;EACE,UAAU;EACV,YAAY;EACZ,sCAAsC;EACtC,kBAAkB;EAClB,gBAAgB;EAChB,gBAAgB;AAClB;;AAEA,mBAAmB;AACnB;EACE,aAAa;EACb,2BAA2B;EAC3B,mBAAmB;EACnB,eAAe;AACjB;;AAEA;;EAEE,WAAW;EACX,eAAe;EACf,YAAY;EACZ,6BAA6B;AAC/B;;AAEA,qBAAqB;AACrB;EACE,aAAa;EACb,iBAAiB;EACjB,cAAc;EACd,aAAa;AACf;;AAEA,6BAA6B;AAC7B;EACE,mBAAmB;EACnB,aAAa;EACb,gBAAgB;AAClB;;AAEA,wBAAwB;AACxB;EACE,sBAAsB;AACxB;;AAEA,mCAAmC;AACnC;EACE,eAAe;EACf,YAAY;AACd;;AAEA,yCAAyC;AACzC;EACE,kBAAkB;EAClB,SAAS;EACT,OAAO;EACP,aAAa;EACb,oCAAoC;EACpC,eAAe;AACjB;;AAEA;EACE,YAAY;EACZ,sBAAsB;EACtB,kBAAkB;EAClB,uBAAuB;EACvB,eAAe;EACf,eAAe;AACjB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,qBAAqB;EACrB,yCAAyC;AAC3C","sourcesContent":["/* Main editor container */\r\n.editor-container {\r\n  width: 80%;\r\n  margin: auto;\r\n  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);\r\n  position: relative;\r\n  margin-top: 20px;\r\n  text-align: left;\r\n}\r\n\r\n/* Toolbar styles */\r\n.toolbar {\r\n  display: flex;\r\n  justify-content: flex-start;\r\n  margin-bottom: 10px;\r\n  flex-wrap: wrap;\r\n}\r\n\r\n.toolbar .toolbarBtn,\r\n.toolbar select {\r\n  margin: 5px;\r\n  cursor: pointer;\r\n  border: none;\r\n  background-color: transparent;\r\n}\r\n\r\n/* Main editor area */\r\n#editor {\r\n  padding: 10px;\r\n  min-height: 400px;\r\n  overflow: auto;\r\n  outline: none;\r\n}\r\n\r\n/* Individual editor blocks */\r\n.editor-block {\r\n  margin-bottom: 10px;\r\n  padding: 10px;\r\n  min-height: 40px;\r\n}\r\n\r\n/* Active editor block */\r\n.editor-block.active {\r\n  border: 1px solid blue;\r\n}\r\n\r\n/* Image styles within the editor */\r\nimg {\r\n  max-width: 100%;\r\n  height: auto;\r\n}\r\n\r\n/* Editor footer (word/character count) */\r\n.editor-footer {\r\n  position: relative;\r\n  bottom: 0;\r\n  left: 0;\r\n  padding: 10px;\r\n  background: rgba(255, 255, 255, 0.8);\r\n  font-size: 12px;\r\n}\r\n\r\n.table-selec